import BankService from '@/services/bank/BankService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let BankTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultBankTableFilter: {
      type: String,
      default: null
    },
    defaultBankTableOrderBy: {
      type: String,
      default: null
    },
    countryGeoId: {
      type: String,
      default: null
    },
    localOnly: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      bankTableData: [],
      bankTableFilterTimer: null,
      bankTableFilter: this.defaultBankTableFilter,
      bankTableOrderBy: this.defaultBankTableOrderBy,
      bankTableContainer: 'loading-container',
      bankTableBusy: false,
      bankTableTotalRows: 0,
      bankTableCurrentPage: 1,
      bankTablePerPage: 10
    }
  },
  computed: {
    bankTableNoDataText: function () {
      return this.bankTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    bankTableTotalPages: function () {
      return Math.ceil(this.bankTableTotalRows / this.bankTablePerPage)
    },
    bankTablePagingInfo: function () {
      return this.pagingInfo(this.bankTableCurrentPage, this.bankTablePerPage, this.bankTableTotalRows)
    }
  },
  methods: {
    loadBankTableData (loadingContainerId) {
      let self = this
      this.bankTableBusy = true
      this.bankTableContainer = loadingContainerId || this.bankTableContainer
      this.startLoading(this.bankTableContainer)
      BankService.banks({
        pageIndex: this.bankTableCurrentPage - 1,
        pageSize: this.bankTablePerPage,
        filter: this.bankTableFilter,
        orderByField: this.bankTableOrderBy,
        countryGeoId: this.countryGeoId,
        localOnly: this.localOnly,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.bankTableContainer)
        self.bankTableBusy = false
        self.bankTableTotalRows = response.data.totalRows
        self.bankTableData = response.data.bankList || []
      }).catch(function (error) {
        self.stopLoading(self.bankTableContainer)
        self.bankTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshBankTable () {
      this.loadBankTableData()
    },
    filterBankTable (filter) {
      let self = this
      if (this.bankTableFilter !== filter) {
        clearTimeout(this.bankTableFilterTimer)
        this.bankTableFilterTimer = setTimeout(() => {
          self.bankTableFilter = filter
          self.loadBankTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.bankTableFilterTimer)
      }
    },
    orderBankTable (orderBy, active) {
      this.bankTableOrderBy = (active ? '' : '-') + orderBy
      this.loadBankTableData()
    }
  },
  watch: {
    bankTableCurrentPage: function () {
      this.refreshBankTable()
    },
    bankTablePerPage: function () {
      this.refreshBankTable()
    },
    countryGeoId: function () {
      this.refreshBankTable()
    }
  }
}

export default BankTableMixin
